<template>
  <div class="food">
    <!-- 上方header -->
    <el-menu
      :default-active="nowClick"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#4F4F4F"
      text-color="#fff"
      active-text-color="#AE8F00"
      style="padding: 0 0 0 57%; width: 100vw; position: fixed; z-index: 999"
    >
      <el-image class="mainLogo" :src="require('@/assets/logo.png')"></el-image>
      <el-button type="text" class="mainWord" @click="pageChange('/')"
        >高雄蚵仔寮</el-button
      >
      <el-menu-item index="1" @click="pageChange('/')">關於蚵仔寮</el-menu-item>
      <el-menu-item index="2" @click="pageChange('food')"
        >美食推薦</el-menu-item
      >
      <el-menu-item index="3" @click="pageChange('place')"
        >景點推薦</el-menu-item
      >
      <el-menu-item index="4" @click="pageChange('traffic')">交通</el-menu-item>
      <el-submenu index="5">
        <template slot="title">熱門活動</template>
        <el-menu-item index="5-1" @click="pageChange('activity1')"
          >蚵仔寮漁港風情體驗生活館</el-menu-item
        >
        <el-menu-item index="5-2" @click="pageChange('activity2')"
          >限定活動</el-menu-item
        >
      </el-submenu>
    </el-menu>
    <div class="mainPic">
      <el-image
        :src="require('@/assets/home2.png')"
        style="width: 100%; height: 450px"
      ></el-image>
    </div>
    <div class="content">
      <div class="title">附近景點推薦</div>
      <hr />
      <el-row :gutter="20">
        <el-col :span="12">
          <!-- 地圖 -->
          <l-map class="map" :zoom="zoom" :options="options" :center="center">
            <!-- 載入地圖資料 -->
            <l-tile-layer :url="url" :attribution="attribution" />
            <!-- 地標 -->
            <l-marker
              ref="location"
              :lat-lng="item.local"
              v-for="item in MapData"
              :key="item.name"
            >
              <l-icon :icon-url="iconimg" />
              <!-- 地標圖示 -->
              <l-tooltip :options="tooltipOptions">
                <div class="tooltip-content">
                  <div>{{ item.name }}</div>
                </div>
              </l-tooltip>
            </l-marker>
          </l-map>
        </el-col>
        <el-col :span="12">
          <el-carousel height="440px">
            <el-carousel-item v-for="(item, index) in placeArray" :key="item">
              <div class="carouselName">{{ MapData[index].name }}</div>
              <el-image :src="item"></el-image>
            </el-carousel-item>
          </el-carousel>
        </el-col>
      </el-row>
    </div>
    <Header> </Header>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
export default {
  name: "Home",
  components: {
    Header,
  },
  data() {
    return {
      nowClick: "3",
      placeArray: [
        require("@/assets/place1.jpg"),
        require("@/assets/place2.jpg"),
        require("@/assets/place3.jpg"),
        require("@/assets/place4.jpg"),
      ],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 16,
      //地圖中心
      center: [22.726762, 120.253216],
      // 地標圖示
      iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [0, 20],
      },
      // ---------------------地圖打點資料---------------------
      MapData: [
        {
          name: "蚵仔寮郵局",
          local: [22.728369, 120.255557],
        },
        {
          name: "通安宮",
          local: [22.72842, 120.250694],
        },
        {
          name: "寮南沙灘",
          local: [22.72367, 120.253328],
        },
        {
          name: "蚵仔寮觀光魚市場",
          local: [22.726762, 120.255216],
        },
      ],
    };
  },
  methods: {
    pageChange(page) {
      this.$router.push(page);
    },
  },
};
</script>
<style lang="scss" scoped>
.food {
  overflow-x: hidden;
  overflow-y: auto;
  width: 100vw;
  height: 100vh;
  .title {
    color: #484891;
    font-weight: bold;
    font-size: 28px;
  }
  .mainLogo {
    padding: 5px 0;
    width: 70px;
    height: 50px;
    position: absolute;
    left: 30px;
  }
  .mainWord {
    position: absolute;
    color: white;
    font-size: 40px;
    font-weight: bold;
    letter-spacing: 10px;
    left: 120px;
  }
  .mainPic {
    position: relative;
    top: 0;
    width: 100%;
    height: 450px;
    text-align: center;
  }
  .content {
    padding: 3%;
    .map {
      height: 450px;
      width: 90%;
      margin: 0 auto;
    }
    .title {
      color: #484891;
      font-weight: bold;
      font-size: 28px;
    }
    hr {
      color: #adadad;
      margin: 1% 0 2% 0;
      width: 15%;
    }
    .carouselName {
      text-align: center;
      font-size: 25px;
      margin-bottom: 10px;
      letter-spacing: 10px;
    }
  }
  /deep/.el-menu-item,
  /deep/.el-submenu__title {
    &:hover {
      font-size: 21px;
    }
  }
  /deep/.el-menu-item.is-active {
    font-weight: bold;
    font-size: 22px;
  }
}
</style>