        require("@/assets/otherfood6.jpg"),
        require("@/assets/otherfood7.jpg"),
        require("@/assets/otherfood8.jpg"),
      ],
    };
  },
  methods: {
    pageChange(page) {
      this.$router.push(page);
    },
    mouseoverHandler(index) {
      for (let i = 0; i < this.showDetail.length; i++) {
        this.showDetail[i] = false;
      }
      if (this.showDetail[index] === false) {
        this.$set(this.showDetail, index, true);
      }
    },
    mouseoutHandler() {
      for (let i = 0; i < this.showDetail.length; i++) {
        this.$set(this.showDetail, i, false);
      }
    },
