<template>
  <div class="activity1" @scroll="watchTopheight()">
    <!-- 上方header -->
    <el-menu
      :default-active="nowClick"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#4F4F4F"
      text-color="#fff"
      active-text-color="#AE8F00"
      style="padding: 0 0 0 57%; width: 100vw; position: fixed; z-index: 999"
    >
      <el-image class="mainLogo" :src="require('@/assets/logo.png')"></el-image>
      <el-button type="text" class="mainWord" @click="pageChange('/')"
        >高雄蚵仔寮</el-button
      >
      <el-menu-item index="1" @click="pageChange('/')">關於蚵仔寮</el-menu-item>
      <el-menu-item index="2" @click="pageChange('food')"
        >美食推薦</el-menu-item
      >
      <el-menu-item index="3" @click="pageChange('place')"
        >景點推薦</el-menu-item
      >
      <el-menu-item index="4" @click="pageChange('traffic')">交通</el-menu-item>
      <el-submenu index="5">
        <template slot="title">熱門活動</template>
        <el-menu-item index="5-1" @click="pageChange('activity1')"
          >蚵仔寮漁港風情體驗生活館</el-menu-item
        >
        <el-menu-item index="5-2" @click="pageChange('activity2')"
          >限定活動</el-menu-item
        >
      </el-submenu>
    </el-menu>
    <div class="mainPic">
      <el-image
        :src="require('@/assets/home2.png')"
        style="width: 100%; height: 450px"
      ></el-image>
    </div>
    <div class="content" id="content">
      <div
        :class="topHight <= 420 ? 'animate__animated animate__slideInLeft' : ''"
      >
        <div class="title">蚵仔寮漁港風情體驗生活館</div>
        <hr />
        <el-row class="buiding" :gutter="30" type="flex" align="middle">
          <el-col :span="10">
            <el-image
              :src="require('@/assets/activity.jpg')"
              style="width: 100%; height: 450px"
            ></el-image>
          </el-col>
          <el-col :span="14">
            <div class="childTitle">簡介</div>
            <div>
              本館設立於蚵仔寮漁港內<br />
              主要包含<span class="highlight" style="color: #a42d00"
                >文化體驗</span
              >、<span class="highlight" style="color: #aa7700">實作體驗</span
              >，以及<span class="highlight" style="color: #2f4f4f"
                >相關展覽</span
              >等多展區<br />
              提供許多<span class="highlight" style="color: #886600"
                >產業體驗課程</span
              >，以認識蚵仔寮在地的漁村生活風情和文化<br />
              另外透過與附近學校進行產學合作，館內也將展出學生們的創意作品
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="title">館區介紹</div>
      <hr />
      <div
        class="introduction"
        v-for="(item, index) in themeArray"
        :key="item.title"
      >
        <el-row
          style="margin-bottom: 50px"
          type="flex"
          align="middle"
          justify="center"
        >
          <el-col class="themeTitle" :style="item.color" :span="3">
            {{ item.title }}
          </el-col>
          <el-col
            class="animate__animated animate__pulse"
            :style="{
              'animation-duration': '3s',
              'animation-iteration-count': 'infinite',
            }"
            :span="7"
            v-for="(detail, detailIndex) in item.includes"
            :key="detail.name"
          >
            <div
              class="themeBlock_outside"
              @mouseover="mouseoverHandler(index, detailIndex)"
              @mouseout="mouseoutHandler()"
            >
              <div
                class="themeBlock"
                :style="color[index]"
                v-if="showDetail[index][detailIndex]"
              >
                <div
                  class="themeBloch_inside"
                  v-html="detail.detail"
                  style="font-size: 20px"
                ></div>
              </div>
              <div
                class="themeBlock"
                :style="color[index]"
                v-if="!showDetail[index][detailIndex]"
              >
                <div class="themeBloch_inside">
                  <div
                    v-html="detail.icon"
                    style="font-size: 115px; color: white; margin-bottom: 20px"
                  ></div>
                  <div>
                    {{ detail.name }}
                  </div>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <Header> </Header>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
export default {
  name: "Home",
  components: {
    Header,
  },
  data() {
    return {
      topHight: 0,
      nowClick: "5-1",
      showDetail: [
        [false, false, false],
        [false, false, false],
        [false, false, false],
      ],
      color: [
        { backgroundColor: "#A42D00" },
        { backgroundColor: "#886600" },
        { backgroundColor: "#008B8B" },
      ],
      themeArray: [
        {
          title: "文化體驗",
          color: { color: "#a42d00" },
          includes: [
            {
              name: "蚵仔寮歷史文化館",
              icon: "<i class='el-icon-reading'></i>",
              detail: "收集蚵仔寮各類歷史文物<br />讓你成為道地蚵仔寮文化專家",
            },
            {
              name: "傳統藝術作品館",
              icon: "<i class='el-icon-brush'></i>",
              detail: "囊總蚵仔寮在地藝術家的作品<br />感受滿滿的傳統藝術魅力",
            },
            {
              name: "電影欣賞館",
              icon: "<i class='el-icon-film'></i>",
              detail: "播放各式紀錄片及微電影<br />帶領大家了解更多蚵仔美",
            },
          ],
        },
        {
          title: "實作體驗",
          color: { color: "#aa7700" },
          includes: [
            {
              name: "藝術創作區",
              icon: "<i class='el-icon-present'></i>",
              detail:
                "提供刮沙畫、海鹽彩虹瓶<br />等藝術小物手做體驗<br />享受親子同樂時光",
            },
            {
              name: "AR互動體驗區",
              icon: "<i class='el-icon-magic-stick'></i>",
              detail:
                "結合AR技術<br />讓你體驗晉身在海洋<br />各式魚類悠游在身旁",
            },
            {
              name: "VR互動體驗區",
              icon: "<i class='el-icon-view'></i>",
              detail: "戴上VR眼鏡<br />模擬體驗出海捕魚的有趣體驗",
            },
          ],
        },
        {
          title: "展覽",
          color: { color: "#2f4f4f" },
          includes: [
            {
              name: "學生作品展",
              icon: "<i class='el-icon-edit-outline'></i>",
              detail: "展出與附近學校合作的<br />產學合作成果及作品",
            },
            {
              name: "藝術活動典藏展",
              icon: "<i class='el-icon-collection'></i>",
              detail: "時光廊展出<br />過去蚵仔寮的活動典藏",
            },
            {
              name: "生活商品園區",
              icon: "<i class='el-icon-shopping-cart-2'></i>",
              detail: "滿滿食品、創意小物<br />供民眾購買紀念",
            },
          ],
        },
      ],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 16,
      //地圖中心
      center: [22.726762, 120.253216],
      // 地標圖示
      iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [0, 20],
      },
      // ---------------------地圖打點資料---------------------
      MapData: [
        {
          name: "蚵仔寮郵局",
          local: [22.728369, 120.255557],
        },
        {
          name: "通安宮",
          local: [22.72842, 120.250694],
        },
        {
          name: "寮南沙灘",
          local: [22.72367, 120.253328],
        },
        {
          name: "蚵仔寮觀光魚市場",
          local: [22.726762, 120.255216],
        },
      ],
    };
  },
  methods: {
    pageChange(page) {
      this.$router.push(page);
    },
    mouseoverHandler(index, detailIndex) {
      for (let i = 0; i < this.showDetail.length; i++) {
        for (let j = 0; j < 3; j++) {
          this.showDetail[i][j] = false;
        }
      }
      if (this.showDetail[index][detailIndex] === false) {
        this.$set(this.showDetail[index], detailIndex, true);
      }
    },
    mouseoutHandler() {
      for (let i = 0; i < this.showDetail.length; i++) {
        for (let j = 0; j < 3; j++) {
          this.$set(this.showDetail[i], j, false);
        }
      }
    },
    watchTopheight() {
      this.topHight = document
        .getElementById("content")
        .getBoundingClientRect().top;
    },
  },
  mounted() {
    this.watchTopheight();
  },
};
</script>
<style lang="scss" scoped>
.activity1 {
  overflow-x: hidden;
  overflow-y: auto;
  width: 100vw;
  height: 100vh;
  .title {
    color: #484891;
    font-weight: bold;
    font-size: 28px;
  }
  .mainLogo {
    padding: 5px 0;
    width: 70px;
    height: 50px;
    position: absolute;
    left: 30px;
  }
  .mainWord {
    position: absolute;
    color: white;
    font-size: 40px;
    font-weight: bold;
    letter-spacing: 10px;
    left: 120px;
  }
  .mainPic {
    position: relative;
    top: 0;
    width: 100%;
    height: 450px;
    text-align: center;
  }
  hr {
    color: #adadad;
    margin: 1% 0 2% 0;
    width: 40%;
  }
  .content {
    padding: 3%;
    line-height: 50px;
    font-size: 22px;
    .buiding {
      background-color: #f0ffff;
      border-radius: 30px;
      padding: 2%;
      margin-bottom: 30px;
    }
    .introduction {
      background-color: #fffff0;
      border-radius: 30px;
      padding: 2%;
    }
    .childTitle {
      font-weight: bold;
      font-size: 25px;
    }
    .highlight {
      font-size: 25px;
      font-weight: bold;
    }
    .themeTitle {
      padding-left: 2%;
      letter-spacing: 15px;
      writing-mode: vertical-lr;
      text-align: center;
      font-weight: bold;
      font-size: 30px;
    }
    // 多行字要垂直對齊方法
    // 最外層設置line-height
    // 內層設置height = 外層的line-height
    // 最裡面內文設置自己的line-height和display: inline-block和vertical-align: middle
    .themeBlock_outside {
      line-height: 250px;
    }
    .themeBlock {
      color: white;
      margin: 0 auto;
      text-align: center;
      font-size: 28px;
      height: 250px;
      width: 270px;
      opacity: 0.8;
      &:hover {
        opacity: 1;
      }
    }
    .themeBloch_inside {
      display: inline-block;
      vertical-align: middle;
      line-height: 50px;
    }
  }
  /deep/.el-menu-item {
    &:hover {
      font-size: 22px;
    }
  }
  /deep/.el-menu-item.is-active {
    font-weight: bold;
    font-size: 22px;
  }
  /deep/.el-submenu.is-active {
    /deep/.el-submenu__title {
      font-weight: bold;
      font-size: 22px;
    }
  }
}
</style>